import React from 'react';
import classes from './Burger.module.css'
import BurgerIngredient from './BurgerIngredients/BurgerIngredients'

const burger = (props) => {
    let me = null;

    me = Object.keys(props.data_ingredients).map((key) => {
        return [...Array(props.data_ingredients[key])].map((_,index) => {
            return <BurgerIngredient data_type={key} key={key+index} />
        })
    }).reduce((arr,el) => {
        return arr.concat(el)
    }, []);

    if(me.length === 0){
        me = (<b>Please start adding ingredients</b>)
    }

    return (
        <div className={classes.Burger}>
            <BurgerIngredient data_type="BreadTop" />
            {me}
            <BurgerIngredient data_type="BreadBottom" />
        </div>    
    );
};

export default burger;