import React from 'react'

import classes from './BuildControls.module.css'
import BuildControl from './Control/Control'

const CONTROLS = [
    {type : 'Salad' , Label : 'Salad'},
    {type : 'Bacon' , Label : 'Bacon'},
    {type : 'Cheese' , Label : 'Cheese'},
    {type : 'Meat' , Label : 'Meat'},
];

const BuildControls = (props) => {
    const isDisabled = (type) => {
        return props.data_currentInfo.ingredients[type] <= 0;
    };

    return (
      <div className={classes.BuildControls}>
          <p>Current Price is : <b>${props.data_currentInfo.price.toFixed(2)}</b></p>
          {
            CONTROLS.map((item) => {
                return (
                    <BuildControl
                        data_label={item.Label}
                        data_isDisabled={isDisabled(item.type)}
                        event_addIngredients={() => {props.event_addIngredients(item.type)}}
                        event_removeIngredients={() => {props.event_removeIngredients(item.type)}}
                        key={item.type}/>
                )
            })
          }
          <button 
            onClick={props.event_clickOrder}
            disabled={!props.data_currentInfo.isPurcashable} 
            className={classes.OrderButton}>Order Now</button>
      </div>  
    );
};

export default BuildControls;