import React from 'react'
import Aux from '../../../hoc/Auxilary/Auxilary';
import Button from '../../UI/Button/Button';

const OrderSummary = (props) => {
    let ingredients = props.data_ingredients
    const Summary = Object.keys(ingredients).map((key) => {
            if(ingredients[key] === 0){return null;}
            return(
                <li key={key}>{key} : {ingredients[key]}</li>
            );
        })
    
    return(
        <Aux>
            <h3>Your Order</h3>
            <p>A delicious burger with the following ingredients:</p>
            <ul>
                {Summary}
            </ul>
            <p>Continue to Checkout?</p>
            <Button
                text="Continue"
                type="Success"
                onClick={props.event_continuePurchasing}/>
            <Button
                text="Cancel"
                type="Danger"
                onClick={props.event_cancelPurchasing}/>
        </Aux>
    );
};

export default OrderSummary;