import React, { Component } from 'react'

import Aux from '../../hoc/Auxilary/Auxilary'
import Burger from '../../Components/Burger/Burger'
import BuildControls from '../../Components/Burger/BuildControls/BuildControls'
import Modal from '../../Components/UI/Modal/Modal';
import OrderSummary from '../../Components/Burger/OrderSummary/OrderSummary'

const INGREDIENTS_PRICE = {
    Meat: 1.5,
    Cheese: 0.7,
    Salad: 0.5,
    Bacon: 1
};

export class BurgerBuilder extends Component {
    state  = {
        ingredients : {
            BreadTop: 0,
            Salad: 0,
            Bacon: 0,
            Cheese: 0,
            Meat: 0,
            BreadBottom: 0
        },
        price: 1,
        isPurcashable: false,
        isPurchasing: false
    }

    Handler_addIngredients = (type) => {        
        const updatedIngredients = { ...this.state.ingredients };
        updatedIngredients[type] += 1;

        const updatedPrice = this.state.price + INGREDIENTS_PRICE[type]

        this.setState({
            ingredients : updatedIngredients,
            price : updatedPrice
        })
        this.Handler_isPurcashable(updatedIngredients);
    }

    Handler_removeIngredients = (type) => {        
        const updatedIngredients = { ...this.state.ingredients };
        if(updatedIngredients[type] === 0) {return;}
        updatedIngredients[type] -= 1;

        const updatedPrice = this.state.price - INGREDIENTS_PRICE[type]

        this.setState({
            ingredients : updatedIngredients,
            price : updatedPrice
        })
        this.Handler_isPurcashable(updatedIngredients);
    }

    Handler_isPurcashable = (updatedingredients) => {
        let me = null;
        const updatedData = {...updatedingredients};
        Object.keys(updatedData).map((key) => {
            me += updatedingredients[key];
            return null;
        })
        this.setState({isPurcashable : (me > 0)});
    }

    Handler_isPurchasing = () => {
        this.setState({
            ...this.state,
            isPurchasing : !this.state.isPurchasing
        });
    }

    Handler_cancelPurchasing = () => {
        this.setState({
            ...this.state,
            isPurchasing: false
        })
    }

    Handler_continuePurchasing = () => {
        alert('You Continue Purchasing!');
    }

    render() {
        return (
            <Aux>
                <Modal 
                    show={this.state.isPurchasing}
                    event_clickBackDrop={this.Handler_cancelPurchasing}>
                    <OrderSummary
                        data_ingredients={this.state.ingredients}
                        event_cancelPurchasing={this.Handler_cancelPurchasing}
                        event_continuePurchasing={this.Handler_continuePurchasing}/>
                </Modal>
                <Burger 
                    data_ingredients={this.state.ingredients} />
                <BuildControls
                    data_currentInfo={this.state}
                    event_addIngredients={this.Handler_addIngredients}
                    event_removeIngredients={this.Handler_removeIngredients}
                    event_clickOrder={this.Handler_isPurchasing}/>
            </Aux>
        )
    }
}

export default BurgerBuilder
