import React from 'react'

import classes from './Control.module.css'

const control = (props) => {
    return (
        <div className={classes.BuildControl}>
            <div 
                className={classes.Label}
                >{props.data_label}</div>
            <button
                className={classes.Less}
                onClick={props.event_removeIngredients}
                disabled={props.data_isDisabled}
                >Less</button>
            <button 
                className={classes.More}
                onClick={props.event_addIngredients}
                >More</button>
        </div>
    );
};

export default control;