import React from 'react';
import classes from './SideDrawerToggle.module.css';

const SideDrawerToggle = (props) => {
    return(
        <div className={classes.DrawerToggle} onClick={props.onClick}>
            <div></div>
            <div></div>
            <div></div>
        </div>
    )
}

export default SideDrawerToggle;